import styles from "./getStatus.module.css";

export const getStatus = (status) => {
    switch (status) {
        case "created":
            return <>
                <span className={`${styles.spanCreated}`}></span>
                <span className={`${styles.spanCreated}`}></span>
                <span className={`${styles.spanCreated}`}></span>
                <span className={`${styles.spanCreated}`}></span>
                <span className={`${styles.spanCreated}`}></span>
                <span className={`${styles.spanCreated}`}></span>
            </>


        case "pending":
            return <>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
                <span className={`${styles.spanPending}`}></span>
            </>


        case "done":
            return <>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
                <span className={`${styles.spanDone}`}></span>
            </>

        default:
            return "done"
    }
}

export const getDate = (date) => {
    let DAY;
    const orderDate = new Date(Date.parse(date))
    const timeZone = -orderDate.getTimezoneOffset() / 60
    const currentDay = new Date().getDate()

    switch (orderDate.getDate()) {
        case currentDay:
            DAY = ""
            break

        case currentDay - 1:
            DAY = ""
            break

        case currentDay - 2:
            DAY = "2  "
            break

        default:
            DAY = orderDate.toLocaleDateString("ru-RU", {weekday: "long", day: "numeric"}).toString()
            break
    }

    return `${DAY}, ${orderDate.toLocaleTimeString("ru-RU", {
        hour: "numeric",
        minute: "numeric"
    })} i-GMT${timeZone > 0 && "+"}${timeZone}`
}
